import axios from 'axios';

// API  URL
const BASE_URL = process.env.REACT_APP_API_BASE_URL || 'https://ramzee.online';

//   
export const refreshToken = async () => {
    try {
        const expiredAccessToken = sessionStorage.getItem('accessToken'); //  Access Token
        const response = await axios.post(`${BASE_URL}/api/v1/auth/refresh-token`, 'POST', {
            headers: {
                Authorization: `Bearer ${expiredAccessToken}`, //   
            },
        });
        console.log('   :', response.data.data.accessToken);
        //   
        sessionStorage.setItem('accessToken', response.data.data.accessToken);
        console.log('   :', response.data.data.accessToken);
        return response.data.data.accessToken;
    } catch (error) {
        console.error('  :', error);

        //   :     
        sessionStorage.removeItem('accessToken');
        window.location.href = '/login';
        throw error;
    }
};

// API  
export const apiRequest = async (url, method, data = null, requiresAuth = true) => {

    try {
        //       
        let accessToken = null;
        if (requiresAuth) {
          accessToken = sessionStorage.getItem('accessToken');
        }

        console.log('API  :', { url, method, data });

        //  
        const response = await axios({
            url: `${BASE_URL}${url}`,
            method,
            data,
            headers: {
                'Content-Type': 'application/json',
                ...(accessToken && { Authorization: `Bearer ${accessToken}` }), //    
            },
        });
        

        console.log('API  :', response.data);
        return response.data;
    } catch (error) {
        console.error('API  :', error.response.data);
        

        // 401 Unauthorized 
        if (requiresAuth && error.response?.data === "Unauthorized: Authentication failed") {
            console.warn('401 Unauthorized:   ');

            try {
                //  
                const accessToken  = await refreshToken();
                console.log("   2:", accessToken);
                //  
                const retryResponse = await axios({
                    url: `${BASE_URL}${url}`,
                    method,
                    data,
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: `Bearer ${accessToken}`, //   
                    },
                });

                return retryResponse.data;
            } catch (refreshError) {
                console.error('    :', refreshError);
                throw refreshError;
            }
        }
        
        throw error;
    }
    
};