// src/features/game/components/GameOverScreen.jsx
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useGame } from '../../../contexts/GameContext';
import styled from 'styled-components';
  //  roomId 

const GameOverScreen = () => {
  const navigate = useNavigate();
  const { gameState, roomId } = useGame();
  console.log(" GameOverScreen Rendered!", gameState);

  const handleExit = () => {
    if (roomId) {
      setTimeout(() => {
        window.location.href = `/room/${roomId}/game`;  //     
      }, 500);  // 0.5   
    } else {
      setTimeout(() => {
        window.location.href = '/rooms';  //     
      }, 500);
    }
  };
  
  //  console.log 
  console.log('GameOverScreen Rendered:', {
    gameState: gameState,
    reason: gameState.gameOverReason,
    winner: gameState.winner
  });

  const getMessage = () => {
    if (gameState.gameOverReason === 'acorns') {
      return gameState.evilSquirrel 
        ? "   !\n  !" 
        : "  !\n  !";
    }
  
    if (gameState.gameOverReason === 'emergency') {
      return gameState.lastKilledPlayer === gameState.evilSquirrel
        ? "  !\n  !"
        : "  !\n  !";
    }
  
    if (gameState.gameOverReason === 'time') {
      return gameState.lastKilledPlayer === gameState.evilSquirrel
        ? " !   !\n  !"
        : " !    !\n  !";
    }
  
    if (gameState.gameOverReason === 'kill') {
      return "4  !\n  !";
    }
  
    return " !";
  };

  return (
    <OverlayContainer>
      <ContentBox>
        <Title> </Title>
        <Message>{getMessage()}</Message>
        <ExitButton onClick={handleExit}>
           
        </ExitButton>
      </ContentBox>
    </OverlayContainer>
  );
};

const OverlayContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;  /*   z-index   */
`;

const ContentBox = styled.div`
  background-color: rgba(139, 69, 19, 0.95);
  padding: 2rem;
  border-radius: 15px;
  width: 400px;
  text-align: center;
  z-index: 10000;  /* overlay    */
`;

const Title = styled.h2`
  color: white;
  font-size: 2.5rem;
  font-family: 'JejuHallasan';
  margin-bottom: 1.5rem;
`;

const Message = styled.p`
  color: white;
  font-size: 1.2rem;
  font-family: 'JejuHallasan';
  margin-bottom: 2rem;
  white-space: pre-line;
`;

const ExitButton = styled.button`
  background-color: #90EE90;
  color: black;
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 8px;
  font-size: 1.2rem;
  font-family: 'JejuHallasan';
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #98FB98;
  }
`;

export default GameOverScreen;