import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { sendEmailVerification, verifyEmailCode } from '../../../api/email';

const RegisterForm = ({ onRegister, loading }) => {
  const [formData, setFormData] = useState({
    username: '',
    name: '',
    nickname: '',
    email: '',
    password: '',
    confirmPassword: '',
    emailCode: '',
  });

  const [errors, setErrors] = useState({});
  const [isEmailSent, setIsEmailSent] = useState(false);
  const [isEmailVerified, setIsEmailVerified] = useState(false);
  const [emailTimer, setEmailTimer] = useState(0);

  //     (180 )
  useEffect(() => {
    if (emailTimer > 0) {
      const interval = setInterval(() => {
        setEmailTimer((prev) => (prev > 1 ? prev - 1 : 0));
      }, 1000);
      return () => clearInterval(interval);
    }
  }, [emailTimer]);

  //   
  const handleInputChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  //   
  const validateForm = () => {
    const newErrors = {};

    if (!formData.username || formData.username.length < 4)
      newErrors.username = ' 4  ';

    if (!formData.password || formData.password.length < 8)
      newErrors.password = ' 8  ';

    if (formData.password !== formData.confirmPassword)
      newErrors.confirmPassword = '  ';

    if (!formData.email || !/\S+@\S+\.\S+/.test(formData.email))
      newErrors.email = '  ';

    if (!isEmailVerified) newErrors.emailCode = '  ';

    if (!formData.name) newErrors.name = ' ';
    if (!formData.nickname) newErrors.nickname = ' ';

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  //   
  const handleEmailSend = async () => {
    if (!formData.email || !/\S+@\S+\.\S+/.test(formData.email)) {
      setErrors((prev) => ({ ...prev, email: '  ' }));
      return;
    }

    try {
      await sendEmailVerification(formData.email);
      setIsEmailSent(true);
      setEmailTimer(180);
      setErrors((prev) => ({ ...prev, email: '' }));
    } catch (error) {
      setErrors((prev) => ({ ...prev, email: '  : ' + error.message }));
    }
  };

  //   
  const handleEmailVerify = async () => {
    if (!formData.emailCode.trim()) {
      setErrors((prev) => ({ ...prev, emailCode: ' ' }));
      return;
    }
  
    try {
      console.log("   : ", {
        email: formData.email,
        emailCode: formData.emailCode,
      });
  
      const response = await verifyEmailCode(formData.email, formData.emailCode);
  
      console.log("  : ", response);
  
      setIsEmailVerified(true);
      setEmailTimer(0);
      setErrors((prev) => ({ ...prev, emailCode: '' }));
    } catch (error) {
      console.error("   :", error.response?.data || error.message);
      setErrors((prev) => ({ ...prev, emailCode: '  ' }));
    }
  };

  //  
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    console.log("     :", {
        username: formData.username,
        name: formData.name,
        nickname: formData.nickname,
        email: formData.email,
        password: formData.password
    });

    if (!validateForm()) return;

    //      
    onRegister({
        username: formData.username,
        name: formData.name,
        nickname: formData.nickname,
        email: formData.email,
        password: formData.password,
    });
};

  return (
    <FormContainer onSubmit={handleSubmit}>
      <Title></Title>

      {Object.values(errors).map((err, index) => (
        <ErrorText key={index}>{err}</ErrorText>
      ))}

      <Input name="username" placeholder="" value={formData.username}
        onChange={handleInputChange} />
      
      <Input name="password" type="password" placeholder="" value={formData.password}
        onChange={handleInputChange} />

      <Input name="confirmPassword" type="password" placeholder=" " value={formData.confirmPassword}
        onChange={handleInputChange} />

      <Input name="name" placeholder="" value={formData.name}
        onChange={handleInputChange} />

      <Input name="nickname" placeholder="" value={formData.nickname}
        onChange={handleInputChange} />

      <EmailContainer>
        <Input name="email" type="email" placeholder="" value={formData.email} disabled={isEmailVerified}
          onChange={handleInputChange} />
        <EmailButton type="button" onClick={handleEmailSend} disabled={isEmailVerified || isEmailSent}>
          {isEmailSent ? '' : ' '}
        </EmailButton>
      </EmailContainer>

      {isEmailSent && (
        <EmailContainer>
          <Input name="emailCode" placeholder=" 6" value={formData.emailCode} disabled={isEmailVerified}
            onChange={handleInputChange} />
          <EmailButton type="button" onClick={handleEmailVerify} disabled={isEmailVerified}>
            
          </EmailButton>
          {emailTimer > 0 && <Timer>{emailTimer}s</Timer>}
        </EmailContainer>
      )}

      <Button type="submit" disabled={loading}>{loading ? ' ...' : ''}</Button>
    </FormContainer>
  );
};

// ** **
const FormContainer = styled.form`
  background: rgba(139, 69, 19, 0.9);
  padding: 2rem;
  border-radius: 15px;
  width: 400px;
`;

const Title = styled.h2`
  color: white;
  text-align: center;
  margin-bottom: 1rem;
`;

const Input = styled.input`
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 0.5rem;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
`;

const EmailContainer = styled.div`
  display: flex;
  gap: 8px;
  margin-bottom: 0.5rem;
`;

const EmailButton = styled.button`
  background-color: #90EE90;
  color: black;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
`;

const Timer = styled.span`
  color: #ff6b6b;
  font-size: 0.9rem;
`;

const Button = styled.button`
  width: 100%;
  background-color: #2d1810;
  color: white;
  padding: 0.75rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
`;

const ErrorText = styled.p`
  color: red;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
`;

export default RegisterForm;
