// hooks/useKillSystem.js
import { useState, useEffect } from 'react';
import { useGame } from '../contexts/GameContext';

export const useKillSystem = () => {
 const { gameState, killUser, players } = useGame();
 const [killingPlayer, setKillingPlayer] = useState(null);
 const [dragStart, setDragStart] = useState(null);
 const [isDragging, setIsDragging] = useState(false);

 //   
 const handleDragStart = (e, playerId) => {
   //    3 
   if (gameState.evilSquirrel !== true || gameState.fatigue < 3) return;
   
   //    
   if (gameState.killedPlayers.includes(playerId)) return;

   setDragStart({ 
     y: e.clientY, 
     playerId,
     timestamp: Date.now() 
   });
   setIsDragging(true);
 };

 //   
 const handleDragMove = (e) => {
   if (!dragStart || !isDragging) return;
   
   const dragDistance = e.clientY - dragStart.y;
   const dragTime = Date.now() - dragStart.timestamp;

   // 50px   ,   200ms  
   if (dragDistance > 50 && dragTime > 200) {
    const player = players.find((p) => p.id === dragStart.playerId);
     startKillAnimation(player.nickName);
     setDragStart(null);
     setIsDragging(false);
   }
 };

 //   
 const handleDragEnd = () => {
   setDragStart(null);
   setIsDragging(false);
 };

 //   
 const startKillAnimation = async (victimNickname) => {
   setKillingPlayer(victimNickname);
   
   // 3   
   await new Promise(resolve => setTimeout(resolve, 3000));
   
   //   
   killUser(victimNickname);
   setKillingPlayer(null);
 };

 //   
 useEffect(() => {
   if (isDragging) {
     document.addEventListener('mousemove', handleDragMove);
     document.addEventListener('mouseup', handleDragEnd);
   }

   return () => {
     document.removeEventListener('mousemove', handleDragMove);
     document.removeEventListener('mouseup', handleDragEnd);
   };
 }, [isDragging, dragStart]);

 return {
   killingPlayer,
   handleDragStart,
   isKillable: gameState.evilSquirrel === true && gameState.fatigue >= 3,
   isDragging
 };
};